import React, { useState, useRef, useEffect } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { useData } from '../components/DataLoader';
import './Research.css';
import { useLanguage } from '../components/LanguageProvider';

const ProjectCard = ({ project, isExpanded, onToggle }) => {
  const contentRef = useRef(null);
  const [hasOverflow, setHasOverflow] = useState(false);
  const { language } = useLanguage();

  useEffect(() => {
    const checkOverflow = () => {
      if (contentRef.current) {
        const element = contentRef.current;
        const hasOverflow = element.scrollHeight > element.clientHeight + 5;
        setHasOverflow(!isExpanded && hasOverflow);
      }
    };

    checkOverflow();
    
    const observer = new ResizeObserver(() => {
      checkOverflow();
    });

    if (contentRef.current) {
      observer.observe(contentRef.current);
    }

    return () => observer.disconnect();
  }, [project.description, language, isExpanded]);

  const handleCardClick = (e) => {
    if (hasOverflow || isExpanded) {
      onToggle();
    }
  };

  const handleReadMoreClick = (e) => {
    e.stopPropagation();
    onToggle();
  };

  return (
    <Card 
      className={`research-card ${isExpanded ? 'expanded' : ''}`}
      onClick={handleCardClick}
    >
      <div className="research-overlay"></div>
      <Card.Body>
        <Card.Title>{project.titre}</Card.Title>
        <Card.Subtitle className="mb-2 text-muted">{project.sous_titre}</Card.Subtitle>
        <div className="research-description-container">
          <div 
            ref={contentRef}
            className={`research-description-fade ${!isExpanded && hasOverflow ? 'has-overflow' : ''}`}
            style={{ 
              maxHeight: isExpanded ? 'none' : '160px',
              overflow: 'hidden',
              whiteSpace: 'pre-line',
              textAlign: 'justify'
            }}
          >
            <Card.Text>
              {project.description}
            </Card.Text>
          </div>
          {hasOverflow && !isExpanded && (
            <button
              className="research-read-more-btn"
              onClick={handleReadMoreClick}
            >
              {language !== 'fr' ? 'Read more' : 'Lire la suite'}
            </button>
          )}
        </div>
      </Card.Body>
    </Card>
  );
};

function Research() {
  const { data, loading, error } = useData();
  const [expandedProject, setExpandedProject] = useState(null);
  const { language } = useLanguage();

  if (loading) return <Container className="my-5"><h2>{language !== 'fr' ? 'Loading...' : 'Chargement...'}</h2></Container>;
  if (error) return <Container className="my-5"><h2>{language !== 'fr' ? 'Loading error' : 'Erreur de chargement'}</h2></Container>;
  if (!data.research || data.research.length === 0) return <Container className="my-5"><h2>{language !== 'fr' ? 'No project available' : 'Aucun projet disponible'}</h2></Container>;

  return (
    <Container className="my-5">
      <h1>{language !== 'fr' ? 'Our Research' : 'Nos Recherches'}</h1>
      <br />
      <Row>
        {data.research.map((project, index) => (
          <Col md={6} key={index} className="mb-4">
            <ProjectCard
              project={project}
              isExpanded={expandedProject === index}
              onToggle={() => setExpandedProject(
                expandedProject === index ? null :index
              )}
            />
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Research;